.navigation {
    display: flex;
    align-items: center;
    min-width: 140px;
}

.navigationList {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 0;
}

a {
    text-decoration: none;
}

.navigationNavLink {
    list-style: none;
    color: #ff6060;
    font-size: 14px;
}

.active {
    text-decoration: underline ;
    color: #ff6060;
}

@media screen and (min-width: 420px) {
    .navigationNavLink {
        font-size: 22px;
    }
    .navigation {
        min-width: 200px;
    }
}

